	module.exports = {"data":{"post":{"html":"<p>Today I found out what happens on a virtual server when the disk space is full and a piece of spam mail arrives.</p>\n<ol>\n<li>The spam gets filtered to the spam folder</li>\n<li>The mail cannot be saved, so return to sender</li>\n<li>The sender is the spam filter, namely me. Send an error message via mail</li>\n<li>The mail cannot be saved, so return to sender</li>\n<li>Repeat <i>ad infinitum</i></li>\n</ol>\n<p>At which point I opted to resize my diskspace. 45 minutes later, we were back online.</p>","fields":{"title":"Adventures in disaster recovery","date":"22 Jul 2003","url":"https://papascott.de/2003-07-22-adventures-in-disaster-recovery/"}}},"pathContext":{"slug":"/2003-07-22-adventures-in-disaster-recovery/","prev":{"fields":{"title":"Would you trust an airline with this map?","date":"21 Jul 2003","slug":"/2003-07-21-would-you-trust-an-airline-with-this-map/"}},"next":{"fields":{"title":"Announcing Blosxom 2.0","date":"22 Jul 2003","slug":"/2003-07-22-announcing-blosxom-20/"}}}}

